


.home {
  padding: 0;
  text-align: center;
  overflow: hidden;
  background-image: 
  url('./images/house.JPG') ;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.searchbar {

  display: flex;
